@import url(https://fonts.googleapis.com/css?family=Neucha&display=swap);
html, body {
  margin: 0;
  padding: 0;
  justify-content: center;   
  background-color: #230D46;
  background-attachment: fixed;
  font-family: 'Neucha', cursive;
}
button, input {
  outline: none;
}



h1 {
  font-size: 8vw;
  color: #E47353;
  margin: auto;
  text-align: center;
}

.circles {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.buttons {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}

button {
  margin: 8px;
  color: #FFFADE;
  padding: 20px;
  border-radius: 35px;
  font-size: 1.2em;
  font-weight: 600;
  letter-spacing: 0.3rem;
  /*box-shadow: 0 4px 8px 0 rgba(248, 246, 246, 0.5), 0 6px 20px 0 rgba(247, 245, 245, 0.5);*/
  border: 5px solid #FFFADE;
  font-family: 'Neucha', cursive;
}

button.start {
  background-color: #007E6A;
}

button.stop {
  background-color: #E47353;
}

button:active {
  box-shadow: 0 4px 8px 0 rgba(248, 246, 246, 0.5), 0 6px 20px 0 rgba(247, 245, 245, 0.5);
}

div.score {
  font-size: 2rem;
  letter-spacing: 0.5rem;
  color:#FFFADE;
  font-weight: 600;
  text-align: center;
  margin: 3%;
  white-space: pre-wrap;
}


.circle {
    height: 150px;
    width: 150px;
    margin: 20px;
    border-radius: 50%;
    display: inline-block;
    text-align: center;
    background-color: aliceblue;
    background-size: cover;
    cursor: url(data:image/png;base64,), auto;
    
}
.circle:active {
    box-shadow: 0 4px 8px 0 rgba(248, 246, 246, 0.5), 0 6px 20px 0 rgba(247, 245, 245, 0.5);
   
}
.circle.active {
    background-image: url(/react-apps-speedgame/static/media/mouse.fd3f395f.png);
    box-shadow: inset 0 0 15px rgba(247, 245, 245, 0.5);
}

.gameOver {
    display: flex;
    justify-content: center;
    align-items: center;
    position: fixed;
    height: 100%;
    width: 100%;
    z-index: 100;
    
    background-color: rgba(35, 129, 160, 0.452);
}
.popUpBox {
    position: relative;
    width: auto;
    height: auto;
    margin: 100px 0 0 0;
    padding: 0 5%;
    justify-content: center;
    text-align: center;
    background-color: #FFFADE;
    border-radius: 5px;
    font-size: 2em;
    color: rgba(35, 129, 160);
}

.popUpBox_button {
    position: absolute;
    margin: 0;
    top: 0;
    right: 0;
    outline: none;
    background-color: transparent;
    color: rgb(160, 35, 35);
    border: none;
    font-size: 1em;
}
.popUpBox-text {
    margin-top: 20%;
}
.speedSelectionSection {
  width: 100%;
  left: 0;
  top: 20%;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  color: #FFFADE;  
  font-size: 2em;
}
button.speedButton {
    background-color: transparent;
    font-size: 1.2rem;
}

